export default{    
    data() {
        return {
            pri:"Id",
            ds1:[
                // {id:1,ten:'thuc 101'},
                // {id:2,ten:'thuc 102'}
            ]
        };
    },
    created: function () {
    },
    methods: {
        SetPri(pri){
            this.pri=pri;
        },
        SetDs(ds){
            this.ds1=[];
            ds.forEach(x => {
                let item = JSON.parse(JSON.stringify(x));
                item.Note = '';
                this.ds1.push(item);
            })
        },
        Lds(){
            return this.ds1;
        },
        createId() {
            let max = 0;
            this.ds1.forEach(x => {
              if (x[this.pri] > max) {
                max = x[this.pri];
              }
            })
            return max + 1;
          },
        Add(item){
            let r = JSON.parse(JSON.stringify(item));
            r.Note = "new";
            this.ds1.push(r);
        },
        Find(id){
            let index= this.ds1.findIndex(x=>x[this.pri]==id);
            if( index >=0 ){
                return this.ds1[index];
            }
            return null;
        },
        Update(r){
            let item=this.Find(r[this.pri]);
            if(item!=null){
                let temp=JSON.parse(JSON.stringify(item));
                let keys=Object.keys(r);
                keys.forEach(x=>{
                    temp[x]=r[x];
                })
                if(temp.Note==''){
                    temp.Note="edit";
                }
                let index=this.ds1.findIndex(x=>x[this.pri]==temp[this.pri]);
                this.ds1[index]=temp;
            }
        },
        Del(item){
            let id = item[this.pri];
            let r=this.Find(id);
            if(r!=null){
                let index=this.ds1.findIndex(x=>x[this.pri]==id);
                if(r.Note=='new'){
                    this.ds1.splice(index,1);
                }else{
                    r.Note="del";
                    this.ds1[index]=r;
                }
            }
        },
    }
};
